const duration = 2000;
import md5 from '@/js_sdk/ccdzhang-dokey/md5.js';
import http from '@/components/utils/http.js';
import { mapState, mapMutations } from 'vuex';

export default {
	components: {},
	data() {
		return {
			backpage: '../index/home',
			loginMode: 1,
			usernameType: 'text',
			codeBut: '获取验证码',
			codeClick: true,
			mobile: '',
			password: '',
			code: ''
		};
	},
	computed: mapState(['uerInfo','hasLogin']),
	methods: {
		...mapMutations(['login']),

		loginCode() {
			this.loginMode = 2;
		},
		loginmobile() {
			this.loginMode = 1;
		},
		// 手机号输入
		mobileInput(e) {
			this.mobile = e.detail.value;
		},
		// 密码输入
		passwordInpur(e) {
			this.password = e.detail.value;
		},
		// 验证码输入
		codeInput(e) {
			this.code = e.detail.value;
		},
		// 获取验证码
		getCode() {
			var _that = this;

			if (_that.codeClick) {
				if (_that.mobile[0] != 1 || _that.mobile.length != 11) {
					this.$api.msg('请输入正确的手机号');
					return;
				} else {
					_that.codeClick = false;
					// 发送短信
					_that._sendmsm(_that.mobile);
					// 倒计时开始
				}
			}
		},
		// 点击登录
		loginUp() {
			var _that = this;
			if (_that.mobile == '') {
				this.$api.msg('请输入手机号码');
				return;
			}else if(!/(^1[3|4|5|7|8][0-9]{9}$)/.test(_that.mobile)){
				this.$api.msg('手机号码格式不正确');
				return;
			}  else if (_that.password == '' && _that.loginMode == 1) {
				this.$api.msg('请输入密码');
				return;
			} else if (_that.code == '' && _that.loginMode == 2) {
				this.$api.msg('请输入验证码');
				return;
			} else {
				const data = {
					loginmode: _that.loginMode,
					mobile: _that.mobile,
					password: md5(md5(_that.password)),
					code: _that.code
				};
				this._logonrequest(data);
			}
		},

		_logonrequest(userdata) {
			let opts = {
				url: '/userApi/logon/',
				method: 'post'
			};
			let param = userdata;
			http.httpRequest(opts, param).then(
				res => {
					//打印请求返回的数据
					if (res.data['code'] == 0) {
						this.login(res.data['token']);
						uni.navigateTo({
							url: this.backpage
						});
					} else {
						this.$api.msg(res.data.msg);
					}
				},
				error => {
					console.log(error);
				}
			);
		},
		_sendmsm(mobile) {
			var _that = this;

			let opts = {
				url: '/CommonApi/sendmsm/',
				method: 'post'
			};
			let param = { mobile: mobile, sendtype: 'logon' };
			http.httpRequest(opts, param).then(
				res => {
					//打印请求返回的数据
					if (res.data['code'] == 0) {
						var daojishi = 60;
						var xianshi = setInterval(function() {
							_that.codeBut = '重新发送' + daojishi;
							daojishi--;
							if (daojishi <= 0) {
								_that.codeBut = '重新发送';
								_that.codeClick = true;
								clearInterval(xianshi);
							}
						}, 1000);
					} else {
						this.$api.msg(res.data.msg);
					}
				},
				error => {
					console.log(error);
					uni.showModal({
						content: err.errMsg,
						showCancel: false
					});
				},
				complete => {
					this.loading = false;
				}
			);
		},
		weixinlogin(){
			uni.login({
			  provider: 'weixin',
			  success: function (loginRes) {
			    console.log(loginRes.authResult);
			    // 获取用户信息
			    uni.getUserInfo({
			      provider: 'weixin',
			      success: function (infoRes) {
					console.log(infoRes)
			        console.log('用户昵称为：' + infoRes.userInfo.nickName);
			      }
			    });
			  }
			});
		},
		
	},
	
	
	onLoad(options) {
		if (global.islogon()) {
			uni.redirectTo({
				url: '../person/home',
			});
		}
		if (options.backpage) {
			this.backpage = options.backpage;
		}
		
	}
};
